/**
 * 与 main 相关的业务接口
 */
import http from '@/utils/http'

function getWorkerGroupsApi ({commit}, data) {
  const url = `r/api?sysName=workerGroupManagementService&apiName=queryWorkerGroups&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}

function getWorkerInfoApi ({commit}, data) {
  const url = `r/api?sysName=workerGroupManagementService&apiName=queryWorkerInfo&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}

function workOperateApi ({commit}, data) {
  const url = `r/api?sysName=workerGroupManagementService&apiName=startOrFinishWork&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}

export default {
  getWorkerGroupsApi,
  getWorkerInfoApi,
  workOperateApi,
}



