/**
 * 与 main 相关的业务接口
 */
import http from '@/utils/http'
// 员工上班校验
function checkWorker ({commit}, data) {
  const url = `r/api?sysName=reportMoService&apiName=checkWorker&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}
