/**
 * 与 main 相关的业务接口
 */
import http from '@/utils/http'

function getDoInfoApi ({commit}, data) {
  const url = `r/api?sysName=demandorder&apiName=getDoInfo&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}

export default {
  getDoInfoApi,
}



