// https://cn.vuejs.org/v2/api/#Vue-directive
import Vue from 'vue'

Vue.directive('example', (el, binding) => {
  console.log(el)
  console.log(binding)
})

Vue.directive('focus', {
  // 当被绑定的元素插入到 DOM 中时……
  inserted: function (el) {
    // 聚焦元素
    el.focus()
  },
})



