/**
 * 与 main 相关的业务接口
 */
import http from '@/utils/http'

// 获取我的订单列表接口
function getOrderListApi ({commit}, data) {
  const url = `r/api?sysName=doService&apiName=getDoInfo&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}

