/**
 * 与 车间默认设置 相关的业务接口
 */
import http from '@/utils/http'

// 获取默认信息接口（位置，物料，其他三个页面）
function getDefaultDataApi ({commit}, data) {
  const url = `r/api?sysName=WorkShopDefaultSettingService&apiName=queryDefaultData&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}

