import http from '@/utils/http'

// 获取LOV选项数据接口
function getLovInfoApi ({commit}, data) {
  const url = `r/api?sysName=commonService&apiName=queryData&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}

export default {
  getLovInfoApi,
}



