/**
 * 与 工作日历 相关的业务接口
 */
import http from '@/utils/http'

// 查询所有工作日历
function queryAppCalendarApi ({commit}, data) {
  const url = `r/api?sysName=calendarService&apiName=queryAppCalendar&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}

