import hmapLogin from '@/platform/hmap/login'
export default {
  name: 'App',
  provide () {
    return {
      reload: this.reload,
    }
  },
  data () {
    return {
      routerTransition: 'router-slide-left',
      isRouterAlive: true,
      isLogin: false,
      includeArray: [
        'OrderDetail',
        'ScreenPage',
        'QueryShipTask',
        'RunShip',
        'SearchTask',
        'SearchData',
        'SearchOrder',
        'SearchCondition',
        'ProcessList',
        'PqcReport',
        'ProcessQuality',
        'WritePqcReport',
        'Cockpit',
        'SkipOrderDetail',
        'PurchaseOrder',
        'MoFilter',
        'MaterialStorage',
        'AcceptGoods',
        'AcceptItem',
        'CheckGoods',
        'AcceptWithPo',
        'AcceptMateriel',
        'MaterialList',
        'EditMaterial',
        'RecordList',
        'RecordList',
        'PurchaseOrderApproval',
        'PurchasePendList',
        'NoTaskPickCreate',
        'PickListSearch',
        'PickListFilter',
        'PickListCancel',
        'PickListExecute',
      ],
    }
  },
  computed: {
  },
  watch: {
    $route (to, from) {
      const toDepth = to.path.split('/').length
      const fromDepth = from.path.split('/').length
      this.routerTransition = toDepth < fromDepth ? 'router-slide-right' : 'router-slide-left'
    },
  },
  async mounted () {
    try {
      await hmapLogin('local', true)
      this.isLogin = true
    } catch (err) {
      console.error('app 获取数据出错')
      console.error(err)
    }
  },
  methods: {
    reload () { // 用于提交返回刷新页面的场景
      this.isRouterAlive = false
      this.$nextTick(function () {
        this.isRouterAlive = true
      })
    },
  },
}



