export default {
  // searchParams: {}, // pqc 部分筛选台哦检收集
  pushMaterial (state, params) {
    state.materialList.push(params)
    console.log(JSON.stringify(state.materialList))
  },
  ClearMaterial (state, params) {
    state.materialList = []
    console.log(JSON.stringify(state.materialList))
  },
  deleteMaterial (state, index) {
    // state.materialList = params
    state.materialList.splice(index, 1)
    console.log(JSON.stringify(state.materialList))
  },
  changeReceiveWorkerId (state, params) {
    state.receiveWorkerId = params
  },
}



