/**
 * 与 main 相关的业务接口
 */
import http from '@/utils/http'

// 获取订单排期列表
function getInfoListApi ({commit}, data) {
  const url = `r/api?sysName=orderSchedulingService&apiName=doInfoList&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}

