/**
 * 与 main 相关的业务接口
 */
import http from '@/utils/http'

function getShipTaskApi ({commit}, data) {
  const url = `r/api?sysName=deliveryOrderService&apiName=queryDeliverOrder&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}

