/**
 * 与 main 相关的业务接口
 */
import http from '@/utils/http'

// 获取客户物料接口
function queryItemInfomApi ({commit}, data) {
  const url = `r/api?sysName=mainPageService&apiName=queryItemInfo&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}

