/**
 * 采购订单业务接口
 */

import http from '@/utils/http'

// 获取采购订单列表接口
function getPurchaseList ({commit}, data) {
  const url = `r/api?sysName=purchaseOrderService&apiName=getPoList&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}

