export default {
  name: '',
  components: {
  },
  props: {
    /**
       * 传入的按钮对象
       * 例子:
       * footerButtons: [
        {
          text: '保存',
          eventName: 'save',
        },
        {
          text: '提交',
          eventName: 'submit',
        },
      ],
       */
    buttons: {
      type: Array,
      default: () => [],
    },
  },
  data () {
    return {
    }
  },
  mounted () {
  },
  methods: {
    clickEvent (item) {
      this.$emit(item.eventName)
    },
  },
}



