/**
 * 与材料入库相关的业务接口
 */
import http from '@/utils/http'
// 送货单列表查询接口
function selectDelivery ({commit}, data) {
  const url = `r/api?sysName=usuallyService&apiName=getSimTicket&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}
