/**
 * 与 报工(materiel文件夹) 相关的业务接口
 */
import http from '@/utils/http'

// 常用物料加载接口
function getMaterielLoadApi ({commit}, data) {
  const url = `r/api?sysName=itemsReportsService&apiName=getDefaultWkc&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}
// 统计页面查询-接口
function getCountInfoApi ({commit}, data) {
  const url = `r/api?sysName=itemsReportsService&apiName=countItems&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}
// 记录物料报工信息接口
function getRecordInfoApi ({commit}, data) {
  const url = `r/api?sysName=itemsReportsService&apiName=saveItemExe&organizationId=${window.localStorage.organizationId}`
  return new Promise((resolve, reject) => {
    http.post(url, data).then(response => {
      resolve(response)
    }).catch(err => {
      reject(err)
    })
  })
}
export default {
  getMaterielLoadApi,
  getCountInfoApi,
  getRecordInfoApi,
}



