export default {
  // searchParams: {}, // pqc 部分筛选台哦检收集
  pushItem (state, params) {
    state.supplyItems.push(params)
    console.log(state.supplyItems)
  },
  updateItem (state, params) {
    state.supplyItems[params.index] = params.item
  },
  clearItem (state, params) {
    state.supplyItems = []
  },
  deleteItem (state, index) {
    state.supplyItems.splice(index, 1)
  },
}



