import Vue from 'vue'
import Vuex from 'vuex'

import helloWorld from './modules/helloWorld'
import demandOrderApi from './api/demand-order'

import orderListApi from './api/order-list'
import orderApi from './api/order'
import workManagementApi from './api/team-management'
import workShopApi from './api/work-shop'
import materialStorageApi from './api/material-storage'

import materielReportApi from './api/materielReport'
import defaultDataApi from './api/default-data'
import shipTaskDataApi from './api/shipTask'

import productInputApi from './api/product-input'
import myOrderState from './state/myorder-state'
import myOrderMutations from './mutations/myorder-mutations'

import getLovInfoApi from './api/getLovInfo'
import getInventoryApi from './api/inventory-search'
import reportListApi from './api/reportList'
import workShopState from './workShopData/state'
import workShopMutations from './workShopData/mutations'
import workShopActions from './workShopData/actions'
// WMS部分数据
import MaterialStorageState from './materialStorageData/state'
import MaterialStorageMutations from './materialStorageData/mutations'
import moListApi from './api/moList'

import purchaseOrderApi from './api/purchase-order'
import purchaseApprovalApi from './api/purchase-approval'
// 工作日历模块
import calendarApi from './api/calendar'
import calendarData from './modules/workCalendar'

import moListMutations from './mutations/molist'
import moListState from './state/molist'
import inventoryTransferApi from './api/inventoryTransfer'

import purchaseOrder from './modules/purchaseOrder'

import equipmentCheckApi from './api/equipmentCheck'
import equipmentRepairApi from './api/equipmentRepair'
import equipmentStateApi from './api/equipmentState'

// 个人打卡
import personClockApi from './api/personClock'

// 领料单查询
import pickListSearchApi from './api/pickListSearch'
// 无任务式领料单创建
import noTaskPickCreateState from './noTaskPickCreateData/state'
import noTaskPickCreateMutations from './noTaskPickCreateData/mutations'
import createNoTaskPickApi from './api/createnotaskpick'

Vue.use(Vuex)

// https://vuex.vuejs.org/zh-cn/getting-started.html
export default new Vuex.Store({
  modules: {
    helloWorld,
    purchaseOrder,
    calendarData,
  },
  state: {
    ...myOrderState,
    ...workShopState,
    ...moListState,
    ...MaterialStorageState,
    ...noTaskPickCreateState,
  },
  getters: {},
  mutations: {
    ...myOrderMutations,
    ...workShopMutations,
    ...moListMutations,
    ...MaterialStorageMutations,
    ...noTaskPickCreateMutations,
  },
  actions: {
    ...demandOrderApi,

    ...orderListApi,
    ...orderApi,
    ...workManagementApi,
    ...workShopApi,
    ...materialStorageApi,

    ...materielReportApi,
    ...getLovInfoApi,
    ...defaultDataApi,
    ...shipTaskDataApi,

    ...productInputApi,
    ...getInventoryApi,
    ...reportListApi,
    ...workShopActions,
    ...moListApi,

    ...equipmentCheckApi,
    ...equipmentRepairApi,
    ...equipmentStateApi,
    ...calendarApi,
    ...inventoryTransferApi,
    ...personClockApi,

    ...purchaseOrderApi,
    ...purchaseApprovalApi,
    ...pickListSearchApi,
    ...createNoTaskPickApi,
  },
  strict: process.env.NODE_ENV !== 'production', // 严格模式
})



