import { Cell, Group, View, NavBar } from 'hips'

export default {
  name: 'Home',
  components: {
    Cell,
    Group,
    'HipsView': View,
    NavBar,
  },
  data () {
    return {
    }
  },
  mounted () {
  },
  methods: {
    goPage (name) {
      this.$router.push({
        name: name,
      })
    },
  },
}



